function AbstractChainedBatch(db) {
  this._db = db;
  this._operations = [];
  this._written = false;
}

AbstractChainedBatch.prototype._checkWritten = function () {
  if (this._written) throw new Error('write() already called on this batch');
};

AbstractChainedBatch.prototype.put = function (key, value) {
  this._checkWritten();

  var err = this._db._checkKeyValue(key, 'key', this._db._isBuffer);

  if (err) throw err;
  err = this._db._checkKeyValue(value, 'value', this._db._isBuffer);
  if (err) throw err;
  if (!this._db._isBuffer(key)) key = String(key);
  if (!this._db._isBuffer(value)) value = String(value);
  if (typeof this._put == 'function') this._put(key, value);else this._operations.push({
    type: 'put',
    key: key,
    value: value
  });
  return this;
};

AbstractChainedBatch.prototype.del = function (key) {
  this._checkWritten();

  var err = this._db._checkKeyValue(key, 'key', this._db._isBuffer);

  if (err) throw err;
  if (!this._db._isBuffer(key)) key = String(key);
  if (typeof this._del == 'function') this._del(key);else this._operations.push({
    type: 'del',
    key: key
  });
  return this;
};

AbstractChainedBatch.prototype.clear = function () {
  this._checkWritten();

  this._operations = [];
  if (typeof this._clear == 'function') this._clear();
  return this;
};

AbstractChainedBatch.prototype.write = function (options, callback) {
  this._checkWritten();

  if (typeof options == 'function') callback = options;
  if (typeof callback != 'function') throw new Error('write() requires a callback argument');
  if (typeof options != 'object') options = {};
  this._written = true;
  if (typeof this._write == 'function') return this._write(callback);
  if (typeof this._db._batch == 'function') return this._db._batch(this._operations, options, callback);
  process.nextTick(callback);
};

module.exports = AbstractChainedBatch;